.slider-label { 
}

.single-tile-basic {
    /*padding: 1.2em; */
    display: inline-block;
    cursor: pointer;
    transition: border-radius 0.1s linear;
}
.single-tile-basic:hover{
    border-radius: 25%
}
.selected-tile, .selected-tile:hover {
    border-radius:50%
}
.single-tile-basic:last-child {
    border-right: none
}

.level-1-holder { 
    
    border-collapse: collapse;
}
.level-1-holder div{
    border: 1px solid white;
} 
.tiles-holder{
    line-height: 0;
    OVERFLOW: auto;
    height: 62%;
}
#hsl-container-1 {
    display: flex;
    flex-direction: column;
    height: 80%;
    justify-content: space-around;
}
#hsl-container-1 > div{
    font-size: 1em;
    min-height: 60px;
}
.arrow-btn{ 
    margin-right: 16px; 
    cursor: pointer;
}