/* COMMON */
.vertical-padding {
    padding: 8px 0;
}
.bottom-bordered {
    border-bottom: 1px solid #394B59;
}
.top-bordered{
    border-top: 1px solid #394B59;
}

/* SINGLE COMPONENTS */
#hsl-filters-holder {
    width: 100%;
    min-width: 650px;
}
.export-json-btn {
    margin-right: 18px; 
    font-size: 24px; 
    cursor: pointer;
}
.selected-color-panel {
    height: fit-content;
    padding: 8px 24px; 
    text-align: center;
    display: inline-grid;
    font-size: 14px;
    border-radius:2px;
}
.reset-btn {
    margin-right: 18px; 
    font-size: 24px; 
    cursor: pointer;
}

.hsl-container-2{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding: 7px 24px;
   color: rgb(97, 97, 97);
}


div.hr {
    height: 1px;
    background: transparent;
    box-shadow: #0000007a 0px 0px 1px 0px;
  }
  div.hr hr {
    display: none;
  }

  .tiles-holder{
  /*  padding-bottom: 5px;*/
  }
  
  .ant-slider-rail {
      background-color:#ededed;
  }
  
  .ant-slider-track  {
    background: transparent; /*#bababa; */
  }
 .ant-slider-step {
   /* background: rgb(224, 224, 224);*/
    border: none;
    border-radius: 50%;
  }

  .ant-slider-handle:hover {
    background-color: #394b59;
  }

.ant-slider-handle:hover {
    -webkit-box-shadow: 0 0 0 1px rgba(16,22,26,.4);
    box-shadow: 0 0 0 1px rgba(16,22,26,.4);
    background-color: #30404d;
}
  .ant-slider-handle-white {
    -webkit-box-shadow: inset 0 0 0 1px rgba(16,22,26,.2), inset 0 -1px 0 rgba(16,22,26,.1);
    box-shadow: inset 0 0 0 1px rgba(16,22,26,.2), inset 0 -1px 0 rgba(16,22,26,.1);
    background-color: #f5f8fa;
    background-image: -webkit-gradient(linear,left top,left bottom,from(hsla(0,0%,100%,.8)),to(hsla(0,0%,100%,0)));
    background-image: linear-gradient(180deg,hsla(0,0%,100%,.8),hsla(0,0%,100%,0));
    color: #182026;
  }
  .ant-slider-handle:hover {
    -webkit-box-shadow: inset 0 0 0 1px rgba(16,22,26,.2), inset 0 -1px 0 rgba(16,22,26,.1);
    box-shadow: inset 0 0 0 1px rgba(16,22,26,.2), inset 0 -1px 0 rgba(16,22,26,.1);
    background-clip: padding-box;
    background-color: #ebf1f5;
    z-index: 2;
    -webkit-box-shadow: 0 0 0 1px rgba(16,22,26,.2), 0 1px 1px rgba(16,22,26,.2);
    box-shadow: 0 0 0 1px rgba(16,22,26,.2), 0 1px 1px rgba(16,22,26,.2);
    cursor: -webkit-grab;
    cursor: grab;
}
.ant-slider-handle {
    
}

.ant-slider-handle {
    
    -webkit-box-shadow: 0 0 0 1px rgba(16,22,26,.4);
    box-shadow: 0 0 0 1px rgba(16,22,26,.4);
    background-color: white;
    background-image: -webkit-gradient(linear,left top,left bottom,from(hsla(0,0%,100%,.05)),to(hsla(0,0%,100%,0)));
    background-image: linear-gradient(180deg,hsla(0,0%,100%,.05),hsla(0,0%,100%,0));
    color: #f5f8fa;
    position: absolute;
    left: 0;
    border-radius: 3px;
    -webkit-box-shadow: 0 0 0 1px rgba(16,22,26,.2), 0 1px 1px rgba(16,22,26,.2);
    box-shadow: 0 0 0 1px rgba(16,22,26,.2), 0 1px 1px rgba(16,22,26,.2);
    cursor: pointer;
    width: 16px;
    height: 16px;
    border: none;
}

.ant-slider-handle:focus{
    border:none;
    box-shadow: 0 0 0 1px rgba(16,22,26,.2), 0 1px 1px rgba(16,22,26,.2);
}
.ant-slider {
    margin-top: 0;
    margin-bottom: 0;
}

.ant-slider-dot-active {
    box-shadow: 0 0 0 1px rgba(16,22,26,.2), 0 1px 1px rgba(16,22,26,.2);
    border: none;
}

.ant-slider-dot {
    box-shadow: 0 0 0 1px rgba(16,22,26,.2), 0 1px 1px rgba(16,22,26,.2);
    border: none;
    background-color: rgb(249, 250, 251);
    background-image: -webkit-gradient(linear,left top,left bottom,from(hsla(0,0%,100%,.05)),to(hsla(0,0%,100%,0)));
    background-image: linear-gradient(180deg,hsla(0,0%,100%,.05),hsla(0,0%,100%,0));
}

.ant-slider-mark-text-active {
   /* color: rgb(249, 250, 251)*/
}
.ant-slider-mark-text {
    color: rgb(33, 33, 33)
}



#base-color-select-container:hover,
#base-color-select-container:focus{
    box-shadow: 0 5px 5px -3px rgba(0,0,0,.2), 0 8px 10px 1px rgba(0,0,0,.14), 0 3px 14px 2px rgba(0,0,0,.12);
    cursor: pointer;
}
#base-color-select-container{
    box-shadow: 0px 1px 5px 0px rgba(0,0,0,0.2), 0px 2px 2px 0px rgba(0,0,0,0.14), 0px 3px 1px -2px rgba(0,0,0,0.12);
}