.applied-color{ 
    padding: 0.4vw; 
    font-size: 9pt;
}

.applied-colors-container{ 
    display: flex; 
    justify-content: start; 
    flex-wrap: wrap; 
    overflow: auto;
    align-content: baseline;
}
.try-colors-container{ 
    display: flex;
    justify-content: space-between;
    height: 100%
}
.try-card-holder{
    font-size: 1em;
}
#try-background {
    cursor: pointer; 
    padding: 24px;
    height: 100%;
   /* margin: 24px; */
}
.try-title-holder{  
    text-align: center;
    padding-bottom: 12px 
}
.reset-try-card-btn{
    float: right
}
#try-text{
    font-size: 0.8em;
}

.applied-colors-header{
    
    margin-top: 16px;
    display: flex; 
    justify-content: space-between; 
    align-items: center; 
}